.App {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

#drum-machine{
  display: flex;
  align-items: center;
  flex-direction: column;
  font-weight: bold;
}

.drum-pads{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}

.drum-pad{
  padding: 15px;
  margin: 15px;
  border: 1px solid black;
  border-radius: 5px;
  text-align: center;
  font-weight: bold;
  background-color: white;
  cursor: pointer;
}